
































































export default {
	data() {
		return {
			CustomBar: this.CustomBar,
			isLoad:false,
			loadModal: false,
			loadProgress: 0
		};
	},
	methods: {
		isLoading(e) {
			this.isLoad = e.detail.value;
		},
		LoadModal(e) {
			this.loadModal = true;
			setTimeout(() => {
				this.loadModal = false;
			}, 2000)
		},
		LoadProgress(e) {
			this.loadProgress = this.loadProgress + 3;
			if (this.loadProgress < 100) {
				setTimeout(() => {
					this.LoadProgress();
				}, 100)
			} else {
				this.loadProgress = 0;
			}
		}
	}
}
